import * as React from "react";
import Columns from "../../components/columns";
import Layout from "../../components/layout";

import * as styles from "./cloth-and-fluid.module.scss";
import AirDrag from "../../images/cloth/air-drag.mp4";
import NoAirDrag from "../../images/cloth/no-drag.mp4";
import UserInteraction from "../../images/cloth/user-interaction.mp4";
import ClothUserInteraction from "../../images/cloth/cloth-user-interaction.mp4";
import StableFluid from "../../images/cloth/stable_fluid.mp4";
import StableFluidHill from "../../images/cloth/stable-fluid-hills.mp4";
import AirDragFail from "../../images/cloth/air-drag-fail.mp4";
import { Link } from "gatsby";
import Button from "../../components/button";
import Divider from "../../components/divider";

const ClothAndFluid = () => {
  return (
    <Layout pageTitle="Cloth and Fluid Simulation" active="none">
      <h1 className={styles.title}>Cloth and Fluid Simulation</h1>
      <p style={{ fontWeight: "bold" }}>Written by: Jason Woitalla</p>
      <p>
        This is my report of my cloth and fluid simulation. The first half
        documents my cloth simulation that was written in processing. The
        simulation code was written by me and no external libraries were used. A
        few classes were provided to me from my class for 3D camera controls and
        loading in models. All code for the simulation is publicly available on
        my Github.
      </p>
      <Button link="https://github.com/jasonwoitalla/csci5611-projects/tree/project-2">
        Github Code Link
      </Button>
      <Divider />
      <h2>Cloth Simulation</h2>
      <p>
        My cloth simulator was written to be flexible and implement new features
        easily. It is built out to be very object oriented allowing for new
        components to be added easily. It consists of a particle class that can
        manage it's own position, velocity and acceleration. It uses{" "}
        <Link
          to="https://en.wikipedia.org/wiki/Verlet_integration"
          className={styles.link}
        >
          Verlet Integration
        </Link>{" "}
        to remain performant. Particles are connected with the spring class that
        applies spring forces to the particles. From there a cloth is a matrix
        of particles all connected to springs. The videos here featured are from
        very powerful lab computers, but this simulation still runs at 60fps on
        weaker machines with minor timestep tweaks. Here is a video of the full
        simulation running:
      </p>
      <div className={styles.mainVideo}>
        <video loop controls autoplay muted width={640}>
          <source src={UserInteraction} type="video/mp4" />
        </video>
      </div>
      <h3>Rendering</h3>
      <p>
        The simulation has a number of features I would like to highlight. The
        most visual one is the 3D environment the simulation is hosted in. This
        environment features a nice sky color with a fully controllable 3D
        camera. The scene features some basic OpenGL lighting and the cloth has
        a customizable texture featured on it. Any square texture will render on
        the cloth.
      </p>
      <h3>Cloth Physics</h3>
      <p>
        The cloth physics were all coded from scratch using the previously
        mentioned Verlet integration technique. Other important functionality
        for the cloth is the use of 2nd degree springs. Particles in the cloth
        are connected diagonally but they skip over their immediate neighbor.
        This is done to prevent mid air clumping of the cloth and improve its
        overall shape. The cloth also calculates the air drag it experiences to
        make a more realistic simulation. This effect can be seen side-by-side
        in the videos below.
      </p>
      <Columns>
        <div>
          <p style={{ fontWeight: "bold" }}>With Air Drag</p>
          <video loop controls width={600}>
            <source src={AirDrag} type="video/mp4" />
          </video>
        </div>
        <div>
          <p style={{ fontWeight: "bold" }}>Without Air Drag</p>
          <video loop controls width={600}>
            <source src={NoAirDrag} type="video/mp4" />
          </video>
        </div>
      </Columns>
      <h3>User Interaction</h3>
      <p>
        Finally the simulation does have some basic user interaction. With a
        fully controllable 3d camera the user can view the simulation from any
        angle or position. Users can also move their mouse to set a goal point
        for the cloth. At which point whenever the 'g' button is pressed, a hand
        will grab the cloth and drag it to the mouse goal. The hand will grab
        onto a few particles of the cloth and they are no longer affected by
        gravity but have a new goal force applied. This will drag the entire
        cloth into that direction.
      </p>
      <video loop controls width={500}>
        <source src={ClothUserInteraction} type="video/mp4" />
      </video>

      <Divider />
      <h2>Fluid Simulation</h2>
      <p>
        The fluid simulation is a 2d implementation of the{" "}
        <Link
          to="https://en.wikipedia.org/wiki/Shallow_water_equations"
          className={styles.link}
        >
          shallow water equations
        </Link>
        The simulation is rendered in 3d and it has a number of interesting
        starting conditions. There was a huge challenge implementing the stable
        fluids equations into 2d as it becomes complicated to numerically
        integrate in 2 dimensions. The stable fluid equations are also very
        unstable so finding a small timestep and a large dx and dy was
        difficult. However the results look pretty cool and below are two videos
        of different starting positions for the water. It should also be noted
        that there isn't an interesting scene rendered with this simulation
        since most time was spent on just getting the equations to perform well.
        The simulation runs in real time and on an above average computer can
        easily run at 60fps.
      </p>
      <Columns>
        <div>
          <p style={{ fontWeight: "bold" }}>Slanted in both X and Y axis</p>
          <video loop controls width={600}>
            <source src={StableFluid} type="video/mp4" />
          </video>
        </div>
        <div>
          <p style={{ fontWeight: "bold" }}>Sinusoidal Start</p>
          <video loop controls width={600}>
            <source src={StableFluidHill} type="video/mp4" />
          </video>
        </div>
      </Columns>

      <Divider />
      <h3>Challenges</h3>
      <p>
        I faced a lot of challenges in this project dealing with a lot of 2d
        arrays. The cloth simulation had difficulties in tweaking the values to
        get the correct functionality. However, after that it went very well to
        setup the rendering for the cloth. The cloth did need some ground
        friction which took some thought and help to get working correctly but
        now the cloth lands on the floor and stays where it fell. The fluid
        simulation had a lot of difficulties expanding into 2d. One dimension
        was easy enough but expanding the code for the equations introduced a
        lot of new arrays and bugs from all the different loops.
      </p>
      <h3>Tools</h3>
      <p>
        <ul>
          <li>
            <Link to="https://processing.org/" className={styles.link}>
              Processing
            </Link>{" "}
            - Physics and rendering
          </li>
          <li>
            <Link to="https://www.blender.org/" className={styles.link}>
              Blender
            </Link>{" "}
            - 3d modeling
          </li>
        </ul>
      </p>
      <h3>Grading Summary</h3>
      <p>
        Here is the list of attempted parts:
        <ul>
          <li>Multiple Ropes + Cloth Simulation (65 points)</li>
          <li>3D Simulation (10 points)</li>
          <li>High-quality Rendering (5 points)</li>
          <li>Air Drag for Cloth (10 points)</li>
          <li>User Interaction (5 points)</li>
          <br />
          <li>Continuum Fluid Simulation (20 points)</li>
        </ul>
      </p>
      <h3>Fun Failure / Art Contest</h3>
      <p>
        Not sure if this counts towards the art contest but in my attempt to add
        air drag I got this really fun animation of a smiling cloth slowly
        inching down to the ground. I think it looks pretty amusing.
      </p>
      <video loop controls width={500}>
        <source src={AirDragFail} type="video/mp4" />
      </video>
    </Layout>
  );
};

export default ClothAndFluid;
